@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  overflow-x: hidden;
}

main {
  background-color: #d1b899;
  width: 100%;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.results-div {
  background-color: #d1b899;
  gap: 1rem;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
}

.results {
  transition: all .3 ease;
  flex-direction: column;
  align-content: baseline;
  align-items: center;
  height: auto;
  display: grid;
  position: relative;
}

h2 {
  color: #533612;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: Merriweather, serif;
  font-size: 25px;
  font-weight: 600;
}

.search-photo {
  object-fit: cover;
  filter: grayscale(75%);
  align-items: center;
  width: 100%;
  height: 250px;
  padding: 1rem;
}

@media (width <= 600px) {
  h2 {
    padding: .5rem;
    font-size: 20px;
  }

  .results-div {
    flex-direction: column;
    padding: .5rem;
  }

  .search-photo {
    height: 200px;
  }
}

@media (width >= 601px) and (width <= 900px) {
  h2 {
    font-size: 22px;
  }

  .results-div {
    flex-direction: row;
    padding: 1rem;
  }

  .search-photo {
    height: 225px;
  }
}

@media (width >= 901px) and (width <= 1200px) {
  h2 {
    font-size: 24px;
  }

  .results-div {
    padding: 1rem 2rem;
  }

  .search-photo {
    height: 250px;
  }
}

@media (width >= 1201px) {
  h2 {
    font-size: 25px;
  }

  .results-div {
    padding: 1rem 2rem;
  }

  .search-photo {
    height: 250px;
  }
}
